.facts {
    box-sizing: border-box;
    padding: 100px 60px 50px;
    position: relative;

    &-title {
        font-weight: normal;
        font-size: 35px;
        line-height: 41px;
        color: #fff;
        text-align: center;
        margin-bottom: 30px;   
    }

    &-slide-desc {
        margin-top: 40px;
        font-weight: normal;
        font-size: 27px;
        line-height: 32px;
        text-align: center;
        color: #FFFFFF;
    }

    & > .swiper-button-prev, .swiper-button-next {
        color:  #b78a3f;
    }

    & > .swiper-button-prev {
        left: 10px;
    }

    & > .swiper-button-next {
        right: 10px;
    }

    & > .swiper-button-disabled {
        opacity: 0 !important;
    }

    &-pagination {

        display: flex;
        justify-content: center;
        margin-top: 66px;
        

        &-bullet {
        width: 20px !important;
        height: 20px !important;
        border-radius: 50% !important;
        margin: 0 20px !important;
        background-color: transparent !important;
        border: 2px solid #b78a3f !important;
        opacity: 1 !important;
        transition: 0.3s background-color;
    }
       & > .swiper-pagination-bullet-active, {
        background-color: #b78a3f !important;

    }


}
}